<template>
    <div class="container p-4">
        <div class="position-absolute cog p-4" v-if="if_logged_in">
            <button class="btn btn-dark bg-gradient btn-sm" data-bs-toggle="modal" data-bs-target="#servicesProps">
                <font-awesome-icon :icon="['fas', 'cogs']"></font-awesome-icon>
            </button>
        </div>

        <div class="row ps-4 pe-4">
            <div class="col-md-12 text-center mb-4">
                <h2 class="text-dark fw-bold blue-gradient-text">{{ prop_data.title }}</h2>
                <p style="font-size: 15px; letter-spacing: 0.4px;">{{ prop_data.tagline }}</p>
            </div>
        </div>
        
        <div class="row mt-4 mb-4 service-item" v-for="({title, tagline, description, image}, index) in prop_data['service-items']" :key="index">  
            <!-- first item -->
            <div class="col-md-3 d-flex" v-if="index%2 == 0">
                <img class="mx-auto" style="height: 30vh" :src="image" alt="">
            </div>   
                                            
            <div class="col-md-8 col-sm-12 p-4">
                <p class="text-danger text-uppercase mb-0 title">{{ title }}</p>

                <h5 class="text-dark tagline">
                    {{ tagline }}
                </h5> 
                
                <p v-if="description" class ="mb-3 mt-2" style="font-size: 17px;">
                    {{ description }}
                </p>
            </div>  

            <!-- first item -->
            <div class="col-md-3 col-sm-12 d-flex mt-4 order-top" v-if="index%2 == 1">
                <img class="mx-auto" style="height: 30vh" :src="image" alt="">
            </div>   
        </div>
    </div>

    <div class="modal fade" id="servicesProps" tabindex="-1" aria-labelledby="servicesPropsLabel" aria-hidden="true">
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header pb-0 border-bottom-0">
                    <span class="d-block w-100">
                        <h5 class="text-dark text-center">Edit Services Section</h5>
                    </span>
                </div>
                
                <div class="modal-body container">
                    <div class="row">

                        <status v-model:status_data=status_data></status>

                        <form action="#" @submit.prevent="submit(form_data, [false, ''], 'services')" v-show="screen == 1">
                            <div class="row mb-4">
                                <label for="title" class="fw-bold mb-2">Title</label>
                                <div class="input-group">
                                    <input id="title" type="text" class="form-control" v-model="form_data.title" required>
                                </div>
                                <small class="text-muted">Enter your title.</small>
                            </div>

                            <div class="row mb-4">
                                <label for="tagline" class="fw-bold mb-2">Tagline</label>
                                <div class="input-group">
                                    <input id="tagline" type="text" class="form-control" v-model="form_data.tagline" required> 
                                </div>
                                <small class="text-muted">Enter your tagline.</small>
                            </div>

                            <div class="row mb-4">
                                <label for="image" class="fw-bold mb-2">Background color</label>

                                <div class="input-group">
                                    <select id="background_color" class="form-select" v-model="form_data.background_color" required>
                                        <option></option>
                                        <option value="white">White</option>
                                        <option value="light">Light</option>
                                        <option value="dark">Dark</option>
                                        <option value="burgundy">Burgundy</option>
                                    </select>
                                </div>
                                <small class="text-muted">Enter the background color.</small>
                            </div>

                            <div class="row mb-4 mt-2">
                                <div class="col-md-12 mt-2 d-flex">
                                    <button type="submit" class="btn btn-dark w-75 me-2">
                                        Submit
                                    </button>

                                    <button type="button" class="btn bg-secondary text-white w-25" @click="changeScreen(2)">
                                        <font-awesome-icon :icon="['fas', 'angle-right']"></font-awesome-icon>
                                    </button>
                                </div>
                            </div>                              
                        </form>

                        <form action="#"  @submit.prevent="submit(services, [false, ''], 'service-items', true)" v-show="screen == 2">
                            <p class="text-burgundy fw-bold">Add a Service entry</p>
                            <div class="row mb-4">
                                <label class="fw-bold mb-2">Title</label>

                                <div class="input-group">
                                    <input type="text" class="form-control" v-model="services.title" @change="ifUnique('title', $event.currentTarget)" required>
                                </div>
                                <small class="text-muted">Enter your title.</small>
                            </div>  

                            <div class="row mb-4">
                                <label class="fw-bold mb-2">Tagline</label>

                                <div class="input-group">
                                    <textarea rows="2" class="form-control" v-model="services.tagline" @change="ifUnique('tagline', $event.currentTarget)" required> </textarea>
                                </div>
                                <small class="text-muted">Enter your tagline.</small>
                            </div>

                            <div class="row mb-4">
                                <label class="fw-bold mb-2">Description</label>

                                <div class="input-group">
                                    <textarea type="text" class="form-control" v-model="services.description" @change="ifUnique('description', $event.currentTarget)" rows="4"> </textarea>
                                </div>
                                <small class="text-muted">Enter your description.</small>
                            </div> 

                            <div class="row mb-4">
                                <label class="fw-bold mb-2">Image</label>

                                <div class="input-group">
                                    <input type="file" class="form-control" @change="onImageChange($event, 'services')">
                                </div>
                                <small class="text-muted">Enter your description.</small>
                            </div>

                            <div class="row mb-4 mt-2">
                                <div class="col-md-12 mt-2 d-flex">
                                    <button type="submit" class="btn btn-dark w-75 me-2">
                                        Submit
                                    </button>

                                    <span class="w-25 d-flex">
                                        <button type="button" class="btn bg-secondary text-white me-2 w-50" @click="changeScreen(1)">
                                            <font-awesome-icon :icon="['fas', 'angle-left']"></font-awesome-icon>
                                        </button>

                                        <button type="button" class="btn bg-secondary text-white w-50" @click="changeScreen(3)">
                                            <font-awesome-icon :icon="['fas', 'angle-right']"></font-awesome-icon>
                                        </button>
                                    </span>
                                </div>
                            </div>                                                                    
                        </form>

                        <div v-show="screen == 3">
                            <p class="text-burgundy fw-bold">Delete a Service entry</p>

                            <div class="row" v-for="({title, tagline}, index) in prop_data['service-items']" :key="index">

                                <div class="col-md-12 d-flex">
                                    <div class="d-block me-4">
                                        <p class="mb-0 text-dark">&bull; {{ title }}</p>
                                        <p class="text-muted mt-1">{{ tagline }}</p>                                        
                                    </div>
                                    <div class="d-block ms-auto">
                                        <p class="mb-0" style="cursor: pointer" @click="remove(prop_data['service-items'], index, 'service-items')">
                                            <font-awesome-icon class="text-burgundy" :icon="['fas', 'trash-alt']"></font-awesome-icon>
                                        </p>
                                    </div>
                                </div>

                            </div>

                            <div class="col-md-12 mt-2 d-flex">
                                <button type="button" class="btn bg-secondary text-white w-25 me-2" @click="changeScreen(2)">
                                    <font-awesome-icon :icon="['fas', 'angle-left']"></font-awesome-icon>
                                </button>

                                <button type="submit" class="btn btn-dark w-75 me-2">
                                    Submit
                                </button>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<style lang="scss" scoped>
    @import '../../../assets/scss/helpers/_breakpoints.scss';
    .title {
        font-weight: 700;
        letter-spacing: 1.3px;
        font-size: 13px;
    }

    .tagline {        
        font-weight: 500;
        letter-spacing: .3px;
    }

    .order-top {
        @include media('<=tablet') {
            order: -1!important;
        }
    }

    .service-item {
        padding: 0 4.5rem;
         @include media('<=tablet') {
            padding: 0 1.5rem;
        }
    }

</style>

<script>
    import Status from '../../Status';  
    import handleProps from '../../../mixins/handleProps';
    import image from '../../../mixins/image';

    export default {
        name: "Services",
        mixins: [handleProps, image],
        components: {
            Status
        },
        props: {
            prop_data: {
                required: true,
                type: Object
            },
            page: {
                required: true, 
                type: String
            }             
        },
        data() {
            return {
                form_data: {
                    title: this.prop_data.title,
                    tagline: this.prop_data.tagline,
                    background_color: this.prop_data.background_color
                },
                services: {
                    title: '',
                    tagline: '',
                    description: '',
                    image: ''
                },
                status_data: {
                    success: false,
                    success_message: 'Form Submission was successful!',
                    error: false,
                    error_message: 'There are some errors.',
                    loading: false
                }, 
                screen: 1,
                if_logged_in: false
            }
        },
        mounted() {
            this.ifLoggedIn()
        },
        methods: {
            changeScreen(value) {
                this.screen = value
            },
            ifUnique(value, event) {
                if(this.prop_data['service-items'] != null) {
                    var current_event = null

                    this.prop_data['service-items'].forEach(element => {
                        if(element[value].toLowerCase() == this.services[value].toLowerCase()) {
                            current_event = event.parentNode.nextElementSibling

                            this.services[value] = ''

                            current_event.innerHTML = 'This '+ value + ' is already taken. Please choose something unique.'

                            event.parentNode.nextElementSibling.classList.remove('text-muted')

                            event.parentNode.nextElementSibling.classList.add('text-burgundy')
                        }
                    });

                    if(current_event == null && event.parentNode.nextElementSibling.classList.contains('text-burgundy')) {

                        event.parentNode.nextElementSibling.innerHTML = 'Nice!'

                        event.parentNode.nextElementSibling.classList.remove('text-burgundy')

                        event.parentNode.nextElementSibling.classList.add('text-primary')
                    }
                }
            },
            ifLoggedIn() {
                if(localStorage.getItem('user') != null) {
                    this.if_logged_in = true
                } else {
                    this.if_logged_in = false
                }
            }                      
        }
    }
</script>